.clear {
  &-btn {
    border: none;
    background-color: transparent;
  }
  &-input {
    background-color: transparent;
    border-bottom: solid 1px transparent;
    border-top: none;
    border-left: none;
    border-right: none;
    &:focus {
      outline: none;
      border-color: $gray-300;
    }
  }
}