
.f-table {
  color: #2a496e;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-flow: column nowrap;
      -ms-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
      -ms-flex-pack: justify;
          justify-content: space-between;
  // border: 1px solid #222529;
  font-size: $font-size-sm;
  margin: 0 0.5rem;
  line-height: 1.5;
  &__text_lg {
    font-size: 18px;
  }
  &__header_mob {
    .btn {
      float: right;
    }
    .f-table__search {
      float: left;
    }
  }
  &__search {
    span {
      display: inline-block;
      vertical-align: middle;
    }
    .icon-search {
      font-size: 21px;
      color: #57688E;
      vertical-align: middle;

    }
    input {
      font-size: 14px;
      vertical-align: middle;
      width: calc(100% - 30px);
      padding-left: 6px;
      color: #b0bacb;
      &::-webkit-input-placeholder {
        color: #b0bacb;
      }
      &::-moz-placeholder {
        color: #b0bacb;
      }
      &:-ms-input-placeholder {
        color: #b0bacb;
      }
      &:-moz-placeholder {
        color: #b0bacb;
      }
    }
  }
  &__header {
    display: none;
    font-size: $font-size-ms;
    .f-table__row-item {
      min-height: 52px;
      white-space: nowrap;
    }
    i {
      font-size: 8px;
      margin-left: 5px;
    }
  }
  &__row {
    width: 100%;
    &-item {
      &:first-child {
        min-width: 215px;
      }
      min-height: 42px;
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      -webkit-flex-flow: row nowrap;
          -ms-flex-flow: row nowrap;
              flex-flow: row nowrap;
      -webkit-box-flex: 1;
      -webkit-flex-grow: 1;
          -ms-flex-positive: 1;
              flex-grow: 1;
      -webkit-flex-basis: 0;
          -ms-flex-preferred-size: 0;
              flex-basis: 0;
      padding: 0.5em;
      word-break: break-word;
      border-bottom: 1px solid #CECECE;
      // &:before {
      //   content: attr(data-header);
      //   width: 30%;
      // }
    }
  }
}

