//     }
//   }
// }

@media (min-width: 992px) {
  .f-table {
    &__header_mob {
      display: none !important;
    }
      &__search_mob {
        display: none !important;
      }
      &__title {
        display: none !important;

      }
    &__title {
      // display: none !important;
    }
    &__row {
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      -webkit-flex-flow: row nowrap;
          -ms-flex-flow: row nowrap;
              flex-flow: row nowrap;
      &-item {
        padding: 0.5em;
        // &:before {
        //   content: none;
        // }
      }
    }
  }
}
@media (max-width: 575px) {
  .f-table__title {
    width: 100%;
    display: block;
  }
  .f-table__row-item {
    display: block;
    width: 100%;
    text-align: center;
  }
}
@media (min-width: 576px) {
  .f-table__title {
    width: 40%;
    float: left;
    margin-right: 40px;
  }
  
}
@media (max-width: 991px) {
  #map {
    height: 300px !important;
  }
  .f-table__row {
    margin-bottom: 20px;
  }
}


