.card {
  padding: 15px;
  &__title {
    font-size: 14px;
    margin-bottom: 8px;
    font-family: $font-family-sans-serif2;
    &_xl {
      font-family: $font-family-sans-serif2;
      font-size: $title-font-size;
      font-weight: 500;
      color: #2a496e;
      margin-bottom: 10px;
    }
  }
  &_shadow {
    height: 100%;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.1);
    margin-bottom: 15px;
    margin-top: 15px;
    color: #6a7da0;
    font-size: $font-size-sm;
  }
  &_f-table {
    margin-top: 15px;
    height: auto;
    margin-top: 45px;
  }
  &__photo {
    height: auto;
    margin-top: 45px;
    a {
      width: 299px;
      height: 99px;
    }
  }
  &__rating {
    .b-object__item {
      margin-bottom: 25px;
    }
  }
}