.b-rating {
  color: #e06b7a;
  &__text {
    width: 100px;
    display: inline-block;
    font-size: $font-size-sm;
  }
  &__num {
    font-family: $font-family-secondary;
    font-size: 18px;
    span {
      font-size: 24px;
    }
    &_xl {
      font-size: $h4-font-size;
      span {
        font-size: 36px;
        font-weight: $display4-weight;
      }
    }
  }
}