.b-aside {
  &__nav {
    background-color: #22262B;
    padding-top: 5px;
    width: 70px;
    margin-right: 15px;
    &-item {
      margin-bottom: 10px;
      position: relative;
      height: 58px;
      display: table;
      width: 100%;
      &.active {
        &:before {
          position: absolute;
          content: '';
          right: 0;
          top: 0;
          width: 4px;
          height: 58px;
          border-radius: 16px;
          background-color: $white;
        }
      }
    }
    a {
      color: $white;
      text-align: center;
      display: table-cell;
      vertical-align: middle;
      &:hover {
        text-decoration: none;
        color: $cyan;
      }
      &:avive {
        // color: #000;
      }
    }
    i {
      // font-size: $font-size-lg;
      font-size: 25px;
      &.icon-paper-plane {
        font-size: $font-size-base;
      }
    }
    &-txt {
      font-size: $font-size-xs;
    }
  }
}
@media (min-width: 992px) {
  .b-aside {
    &__nav {
      // height: calc(100vh - 56px);
    }
  }
}
@media (min-width: 576px) {

}
@media (max-width: 575px) {
  .b-aside {
    &__nav {
      width: 40px;
      &-txt {
        display: none;
      }
    }
  }
}