.b-header {
  background-color: $dBlue;
  padding-top: 7px;
  padding-bottom: 8px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.19);
  &__logo {
    width: 70px;
    margin-right: 15px;
  }
  &__search {
    margin-left: 15px;
    width: calc(100% - 185px);
    i {
      color: $white;
    }
    input {
      color: $white;
      padding-left: 5px;
      width: calc(100% - 33px);
      &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
        color: $white;
      }
      &::-moz-placeholder { /* Firefox 19+ */
        color: $white;
      }
      &:-ms-input-placeholder { /* IE 10+ */
        color: $white;
      }
      &:-moz-placeholder { /* Firefox 18- */
        color: $white;
      }
    }
  }
}

@media (max-width: 575px){
  .b-header {
    &__logo {
      width: 40px;
      padding-left: 5px;
      padding-right: 5px;
      margin-right: 15px;
    }
  }
}