.b-checkbox {
  position: relative;
  vertical-align: middle;
  display: inline-block;
  line-height: 42px;
  input {
    display: none;
    position: absolute;
    &:checked {
      +label {
        background-color: #2a496e;
      }
    }
  }
  label {
    vertical-align: middle;
    display: inline-block;

    width: 12px;
    height: 12px;
    border-radius: 2px;
    border: solid 1px #2a496e;
    cursor: pointer;
    
  }
}